import React from "react";
const category = [
  {
    name: "Category",
    all: "All"
  },
  {
    name: "Filter",
    all: "All"
  }
];
const Category = () => {
  return category.map(options => {
    return (
      <div className="category">
        <span>{options.name}</span>:{" "}
        <span className="color-blue">{options.all}</span>
      </div>
    );
  });
};
export default Category;
