import React from "react";
import Category from "./Category";
import Favourites from "./Favourites";
import Search from "./Search";

const Integration = props => {
  return (
    <div className="cover-integration padding-major">
      <div className="integration margin-bottom-small">
        <div className="integration-name">Integrations</div>
        <div className="integration-options">
          <Favourites favourite={props.getFavourite} />
          <Category />
        </div>
      </div>
      <Search />
    </div>
  );
};
export default Integration;
