import React from "react";

export default function PopularIntegration(props) {
  const styles_like = {
    style: {
      color: "red",
    },
    classNamed: "  favorite",
  };

  const styles_unliked = {
    style: {
      color: "black",
    },
    classNamed: "  favorite_border",
  };

  let selector = styles_unliked;

  if (props.like == false) {
    selector = styles_unliked;
  } else selector = styles_like;
  return (
    <>
      <div className='card'>
        <div className='card-img'>
          <img src={props.img} className='img' alt='Slack' />
        </div>

        <div className='card-company'>
          <div className='card-name'>
            <span>{props.name}</span>
          </div>
          <div className='card-detail'>
            <span>{props.detail}</span>
          </div>
        </div>

        <div className='card-lower margin-bottom-small'>
          <div className='card-connected'>
            <p>{props.type}</p>
          </div>
          <div className='card-like-delete'>
            <span
              class='material-icons icon-style'
              style={selector.style}
              onClick={props.handleClick}>
              {selector.classNamed}
            </span>
            <div className='card-delete'>
              <span class='material-icons icon-style'>delete</span>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
