import React, { useState } from "react";

const Favourites = props => {
  //   const [fav, setFav] = useState([]);
  const handleFavourites = () => {
    // setFav(prev => prev.concat(props.favourite));
  };

  return (
    <div className="integration-favourites">
      <span onClick={handleFavourites}>Favourites</span>
      <ul className="favorite-list">
        {props.favourite.map(key => {
          return <li className="list">{key}</li>;
        })}
      </ul>
    </div>
  );
};
export default Favourites;
