import React, { useState } from "react";

const options = [
  { icon: "how_to_reg", heading: "Account", detail: "Your account prefrence" },
  {
    icon: "notifications",
    heading: "Notifications",
    detail: "Your notification prefrence"
  },
  {
    icon: "add_box",
    heading: "Integrations",
    detail: "Add or remove integrations"
  },
  {
    icon: "people",
    heading: "Colleagues",
    detail: "Your colleagues prefrence"
  },
  { icon: "label", heading: "Tags", detail: "Add or remove tags" }
];
const Options = () => {
  // const [a, b ] = useState()
  return (
    <div className="options">
      {options.map((option, index) => {
        if (index == 2) {
          return (
            <div className="options-option  border-right">
              <div className="options-icons">
                <span class="material-icons icon-style icon-blue">
                  {option.icon}
                </span>
              </div>
              <div className="options-details">
                <div className="options-heading">{option.heading}</div>
                <div className="options-brief">{option.detail}</div>
              </div>
            </div>
          );
        } else
          return (
            <div className="options-option">
              <div className="options-icons">
                <span class="material-icons icon-style">{option.icon}</span>
              </div>
              <div className="options-details">
                <div className="options-heading">{option.heading}</div>
                <div className="options-brief">{option.detail}</div>
              </div>
            </div>
          );
      })}
    </div>
  );
};
export default Options;
