import React, { useState, useEffect } from "react";
import YourIntegration from "./YourIntegration";
import PopularIntegration from "./PopularIntegrations";

const toConnect = [];

const Cards = props => {
  const [connect, setConnect] = useState([
    {
      id: 1,
      img: "../images/slack.svg",
      name: "Slack",
      detail:
        "Slack is a digital workspace that connects you to the people and tools you work with everyday.",
      type: "Connected",
      like: false
    },
    {
      id: 2,
      img: "../images/facebook.svg",
      name: "Facebook",
      detail:
        "Connect your Facebook Ads account and never struggle to report on the success.",
      type: "Connected",
      like: false
    },
    {
      id: 3,
      img: "../images/zendesk.svg",
      name: "Zendesk",
      detail:
        "Gain more insights into customer experience with the new Zendesk activity integration.",
      type: "Connected",
      like: false
    }
  ]);
  const [toConnect, setToConnect] = useState([
    {
      id: 4,
      img: "../images/mailchimp.svg",
      name: "MailChimp",
      detail:
        "Connect your lead flows and collected forms to your MailChimp account.",
      type: "Connect",
      like: false
    },
    {
      id: 5,
      img: "../images/wordpress.svg",
      name: "WordPress",
      detail:
        "Connect with WordPress blog or website to convert website visitors into leads and buyers.",
      type: "Connect",
      like: false
    },
    {
      id: 6,
      img: "../images/salesforce.svg",
      name: "Salesforce",
      detail:
        "Sync Salesforce for the fast, reliable and powerful integration between your two databases.",
      type: "Connect",
      like: false
    }
  ]);
  const toggleLikeConnect = (arr, index) => {
    let newState = [...arr];
    let like_state = newState[index]["like"];
    if (like_state) {
      newState[index]["like"] = false;
      props.setFav(newState[index]["name"]);
    } else {
      newState[index]["like"] = true;
      props.setFav(newState[index]["name"]);
    }

    localStorage.setItem("likeConnect", JSON.stringify(newState));
    setConnect(newState);

    console.log(newState);
  };

  const toggleLikeToConnect = (arr, index) => {
    let newState = [...arr];
    let like_state = newState[index]["like"];
    if (like_state) {
      newState[index]["like"] = false;
    } else newState[index]["like"] = true;

    localStorage.setItem("likeToConnect", JSON.stringify(newState));
    setToConnect(newState);

    console.log(newState);
  };
  useEffect(() => {
    let connect_copy = localStorage.getItem("likeConnect");
    let toConnect_copy = localStorage.getItem("likeToConnect");

    if (connect_copy) {
      setConnect(JSON.parse(connect_copy));
    }

    if (toConnect_copy) {
      setToConnect(JSON.parse(toConnect_copy));
    }
    // setConnect(JSON.parse(connect_copy));
    //
  }, []);

  return (
    <div className="cards padding-card">
      <div className="cards-connected margin-bottom-small">
        <div className="margin-bottom-small">Your Integrations</div>
        <div className="card-alignment">
          {connect.map((key, index) => {
            return (
              <>
                <YourIntegration
                  id={key.id}
                  img={key.img}
                  name={key.name}
                  detail={key.detail}
                  type={key.type}
                  handleClick={() => {
                    toggleLikeConnect(connect, index);
                  }}
                  like={key.like}
                  delete={key.delete}
                />
              </>
            );

            // else {
            //   return (
            //     <>
            //       <PopularIntegration
            //         id={key.id}
            //         img={key.img}
            //         name={key.name}
            //         detail={key.detail}
            //         type={key.type}
            //         handleClick={handleClick}
            //         like={key.like}
            //         delete={key.delete}
            //       />
            //     </>
            //   );
            // }
          })}
        </div>
      </div>
      <div className="cards-to-connect">
        <div className="margin-bottom-small">Popular Integrations</div>
        <div className="card-alignment">
          {toConnect.map((key, index) => {
            return (
              <>
                <PopularIntegration
                  id={key.id}
                  img={key.img}
                  name={key.name}
                  detail={key.detail}
                  type={key.type}
                  handleClick={() => {
                    toggleLikeToConnect(toConnect, index);
                  }}
                  like={key.like}
                  delete={key.delete}
                />
              </>
            );
          })}
        </div>
      </div>
    </div>
  );
};
export default Cards;
