import React, { useState, useEffect } from "react";
import Cards from "./Cards";
import Integration from "./Integration";
import Search from "./Search";

const Dashboard = () => {
  const [favourite, setFavourite] = useState([]);

  const fav_handler = name => {
    let newState = [...favourite];

    if (newState.includes(name)) {
      newState = newState.filter(element => {
        return element != name;
      });

      console.log(newState);
    } else newState.push(name);
    console.log(name, newState);
    setFavourite(newState);
    localStorage.setItem("favoriteList", JSON.stringify(newState));
  };
  useEffect(() => {
    if (localStorage.getItem("favoriteList")) {
      setFavourite(JSON.parse(localStorage.getItem("favoriteList")));
    }
  }, []);
  return (
    <div className="dashboard">
      <div className="upper">
        <Integration getFavourite={favourite} />
      </div>
      <div className="lower">
        <Cards
          setFav={name => {
            fav_handler(name);
          }}
        />
      </div>
    </div>
  );
};
export default Dashboard;
